<template>
  <div>
    <div id="main" style="width: 300px;height: 300px;"></div>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      charts: "",
      classList: [],

      opinion: ["水果", "蛋糕", "火锅", "鲜花店", "清吧"],
      opinionData: [
        { value: 335, name: "水果" },
        { value: 310, name: "蛋糕" },
        { value: 234, name: "火锅" },
        { value: 135, name: "鲜花店" },
        { value: 100, name: "清吧" }
      ]
    };
  },
  methods: {
    drawPie(id) {
      this.charts = echarts.init(document.getElementById(id));

      this.opinion.map((item, index) => {
        this.$axios
          .get("/vue/goodsClassify", {
            params: {
              type: item
            }
          })
          .then(res => {
            this.classList.push(res.data.result);

            // console.log(this.classList); //找到了合并后的每一个商品的数组

            this.charts.setOption({
              backgroundColor: "#2c343c",
              title: {
                text: "yoya销售数量占比",
                left: "center",
                top: 20,
                textStyle: {
                  color: "#ccc"
                }
              },

              tooltip: {
                trigger: "item",
                formatter: "{a} <br/>{b} : {c} ({d}%)"
              },
              visualMap: {
                show: false,
                min: 80,
                max: 600,
                inRange: {
                  colorLightness: [0, 1]
                }
              },
              legend: {
                orient: "vertical",
                x: "left",
                data: this.opinion
              },
              series: [
                {
                  name: "销售占比",
                  type: "pie",
                  radius: "55%",
                  center: ["50%", "50%"],

                  roseType: "radius",
                  label: {
                    normal: {
                      textStyle: {
                        color: "rgba(255, 255, 255, 0.3)"
                      }
                    }
                  },
                  labelLine: {
                    normal: {
                      lineStyle: {
                        color: "rgba(255, 255, 255, 0.3)"
                      },
                      smooth: 0.2,
                      length: 10,
                      length2: 20
                    }
                  },
                  itemStyle: {
                    normal: {
                      color: "#c23531",
                      shadowBlur: 200,
                      shadowColor: "rgba(0, 0, 0, 0.5)"
                    }
                  },
                  animationType: "scale",
                  animationEasing: "elasticOut",
                  animationDelay: function(idx) {
                    return Math.random() * 200;
                  },
                  data: this.opinionData
                }
              ]
            });
          });
      });
    }
  },
  //调用
  mounted() {
    this.$nextTick(function() {
      this.drawPie("main");
    });
  }
};
</script>

<style lang="scss" scoped>
</style>