<template>
  <div>
    <div id="myChart" style="width:400px;height:400px"></div>
    
  </div>
</template>

<script>
export default {
  name: "hello",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      data:"",
      data1:"",
    };
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));

      this.$axios
        .get("/vue/score1")
        .then(res => {
          this.data = res.data.result;
          // console.log(this.data.length);
        });

          this.$axios
        .get("/vue/score2")
        .then(res => {
          this.data1 = res.data.result;
          // console.log( this.data1.length);
        });
      // 绘制图表
      myChart.setOption({
        title: { text: "评分数量统计" },
        tooltip: {},
        xAxis: {
         data: [1, 2, 3, 4, 5]
        },
        yAxis: {},
        series: [
          {
            name: "评分",
            type: "bar",
             data: [30, 60,46, 80, 150]
            
          }
        ]
      });
    }
  }
};

// 转为小写
